body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-garet: Garet;

  /* font sizes */
  --font-size-13xl: 32px;
  --font-size-21xl: 40px;
  --font-size-17xl: 36px;

  /* Colors */
  --color-lightgray: #d4d4d4;
  --color-white: #fff;
  --color-lightslategray: #7497a1;
  --color-black: #000;
  --color-dimgray: #5a5a5a;

  /* Border radiuses */
  --br-63xl: 82px;
  --br-xl: 20px;
}
